import { API_URL, getUser } from "../../constants/Config";
import * as events from "../events";

export const changePassword = (data) => {
  return async (dispatch, getState) => {
    const token = getState().auth.token;
    const { _id } = data._id;
    delete data._id;
    const body = JSON.stringify(data);
    const response = await fetch(`${API_URL}/users/${_id}`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body,
    });

    if (!response.ok) {
      const errorResData = await response.json();
      let message = "Something went wrong!";
      if (errorResData.error) {
        throw new Error(errorResData.error);
      } else {
        throw new Error(message);
      }
    }

    const resData = await response.json();

    dispatch({
      type: events.SET_PROFILE,
      data: resData.data,
    });
  };
};

export const changeEmail = (data) => {
  return async (dispatch, getState) => {
    const token = getState().auth.token;
    const { _id } = data;
    delete data._id;
    const body = JSON.stringify(data);
    const response = await fetch(`${API_URL}/users/${_id}`, {
      method: "PATCH",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body,
    });

    if (!response.ok) {
      const errorResData = await response.json();
      let message = "Something went wrong!";
      if (errorResData.error) {
        throw new Error(errorResData.error);
      } else {
        throw new Error(message);
      }
    }

    const resData = await response.json();
    dispatch({
      type: events.SET_PROFILE,
      data: resData.data,
    });
  };
};

export const cancelSubscription = () => {
  return async (dispatch, getState) => {
    try {
      const { token } = getUser();
      const response = await fetch(`${API_URL}/users/subscription/cancel`, {
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
      });
      if (!response.ok) {
        const err = await response.json();
        throw new Error(err.error);
      }

      const resData = await response.json();
      if (resData.data) {
        dispatch({
          type: events.SET_PROFILE,
          data: resData.data,
        });
      }

      return resData.data ? resData.data : null;
    } catch (err) {
      // console.log("cancel subscription Error ", err);
      throw err;
    }
  };
};

export const terminateAccount = (invitecode) => {
  return async (dispatch, getState) => {
    try {
      const { token } = getState().auth;
      const response = await fetch(
        `${API_URL}/users/account/terminate/${invitecode}`,
        {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
        }
      );

      if (!response.ok) {
        const err = await response.json();
        throw new Error(err.error);
      }
      const resData = await response.json();
      return resData.success;
    } catch (err) {
      console.log("terminate account Error ", err);
      throw err;
    }
  };
};

export const purchaseCredits = (data) => {
  return async (dispatch, getState) => {
    try {
      const { token } = getUser();
      const response = await fetch(`${API_URL}/users/purchase/credits`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(data),
      });

      if (!response.ok) {
        console.log(await response.json());
        throw new Error("Something went wrong!");
      }
      const resData = await response.json();
      if (resData.data) {
        dispatch({
          type: events.SET_PROFILE,
          data: resData.data,
        });
      }

      return resData.status ? resData.status : null;
    } catch (err) {
      console.log("createPurchaseOrder Error ", err);
      throw err;
    }
  };
};

export const createSubscription = (plan, profile) => {
  return async (dispatch, getState) => {
    try {
      const { token, pm, email } = getUser();
      const response = await fetch(`${API_URL}/users/subscription`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({
          freecredits: plan.freecredits,
          balancecredits: profile.credits,
          price_id: plan.plan_id,
          pm,
          email,
          upgraded: profile.subscribed ? true : false,
        }),
      });

      if (!response.ok) {
        console.log(await response.json());
        throw new Error("Something went wrong!");
      }
      const resData = await response.json();
      if (resData.data) {
        dispatch({
          type: events.SET_PROFILE,
          data: resData.data,
        });
      }
      return resData.data ? resData.data : null;
    } catch (err) {
      console.log("createSubscription Error ", err);
      throw err;
    }
  };
};

export const fetchCredits = (page = 1, limit = 50, invoice = false) => {
  return async (dispatch, getState) => {
    const { token } = getUser();

    let body = {
      skip: page === 1 ? 0 : (page - 1) * limit,
      limit,
      sort: { _id: -1 },
      fields: {
        _id: 1,
        created: 1,
        createdby: 1,
        "createdUser.firstname": 1,
        "createdUser.lastname": 1,
        credits: 1,
        desc: 1,
        pay: 1,
        price: 1,
        team: 1,
      },
    };

    if (invoice) {
      body.filter = {
        pay: { $exists: 1 },
      };
    }

    try {
      const response = await fetch(`${API_URL}/users/credits/history`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify(body),
      });

      if (!response.ok) {
        throw new Error("Something went wrong!");
      }

      const resData = await response.json();

      dispatch({
        type: events.SET_CREDITS,
        data: resData.data,
        count: resData.count,
      });
    } catch (err) {
      // send to custom analytics server
      console.log("Error ", err);
      throw err;
    }
  };
};

export const updateCreditPurchase = (data, socket = null) => {
  return async (dispatch, getState) => {
    try {
      const token = getState().auth.token;
      const response = await fetch(`${API_URL}/users/purchase/credits/update`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify(data),
      });

      if (!response.ok) {
        console.log(await response.json());
        throw new Error("Something went wrong!");
      }

      const resData = await response.json();
      const user = resData.data;
      dispatch({
        type: events.SET_PROFILE,
        data: user,
      });

      if (socket) {
        socket.emit("profile:refresh", {});
      }
      return resData.success ? true : false;
    } catch (err) {
      console.log("updateCreditPurchase Error ", err);
      throw err;
    }
  };
};

